import React from "react";
import Header from "./Header";
import Title from "./Title";
import { Card, Col, Container, Row } from "react-bootstrap";
import project1 from "../Images/project_1.png";
import project2 from "../Images/project_2.png";
import project3 from "../Images/project_3.png";
import project4 from "../Images/project_4.png";
import "../Styles/Portfolio.css";
import Footer from "./Footer";

const Portfolio = () => {
  const projects = [
    {
      name: "Streaming Website",
      img: project1,
      des:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt dolorum qui placeat, ullam mollitia libero.",
    },
    {
      name: "E-Commerce",
      img: project2,
      des:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt dolorum qui placeat, ullam mollitia libero.",
    },
    {
      name: "CRUD Application",
      img: project3,
      des:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt dolorum qui placeat, ullam mollitia libero.",
    },
    {
      name: "Tasty Treat",
      img: project4,
      des:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt dolorum qui placeat, ullam mollitia libero.",
    },
  ];
  return (
    <section id="portfolio" className="portfolio">
      <Header />
      <Container>
        <Title title={"My latest work"} />
        <Row className="portfolio-row">
          {projects.map((project) => {
            return (
              <Col md={{ span: 6 }} className="Projects">
                <div className="projects-data">
                  <Card className="project-card">
                    <img src={project.img} alt="" width="100%" />
                  </Card>
                  <div className="visit">
                    <p className="mb-0 name text-warning fw-bold">
                      {project.name}
                    </p>
                    <p className="mb-0">{project.des}</p>
                    <a href={project.github}>Github</a>
                    <a href={project.live}>Live</a>
                  </div>
                </div>
              </Col>
            );
          })}
        </Row>
        <Footer />
      </Container>
    </section>
  );
};

export default Portfolio;
