import React from "react";
import "../Styles/About.css";
import Header from "./Header";
import { Card, Col, Container, Row } from "react-bootstrap";
import Title from "./Title";
import { FaGraduationCap, FaLandmark, FaMapMarkerAlt, FaRegCalendarAlt, FaUserAlt } from "react-icons/fa";

import {
  SiBootstrap,
  SiCss3,
  SiHtml5,
  SiJavascript,
  SiReact,
  SiRedux,
  SiGithub,
  SiGit,
  SiJquery,
} from "react-icons/si";
import Footer from "./Footer";

const About = () => {
  const details = [
    {
      open: <FaUserAlt />,
      des: "Mohamed El-Desouky El-Sayed El-Mesery",
    },
    {
      open: <FaRegCalendarAlt />,
      des: "09/11/1994",
    },
    {
      open: <FaMapMarkerAlt />,
      des: "Al-Mahallah Al-Kubra, Egypt 31962",
    },
    {
      open: <FaGraduationCap />,
      des: "Bachelor of Communication Engineering",
    },
    {
      open: <FaLandmark />,
      des: "Canadian International College",
    },
  ];

  const skills = [
    {
      icon: <SiHtml5 />,
      name: "HTML",
      per: "85%",
    },
    {
      icon: <SiCss3 />,
      name: "CSS",
      per: "80%",
    },
    {
      icon: <SiJquery />,
      name: "Jquery",
      per: "80%",
    },
    {
      icon: <SiJavascript />,
      name: "JavaScript",
      per: "70%",
    },
    {
      icon: <SiBootstrap />,
      name: "Bootstrap",
      per: "80%",
    },
    {
      icon: <SiReact />,
      name: "React",
      per: "60%",
    },
    {
      icon: <SiRedux />,
      name: "Redux",
      per: "70%",
    },
    {
      icon: <SiGithub />,
      name: "GitHub",
      per: "70%",
    },
    {
      icon: <SiGit />,
      name: "Git",
      per: "50%",
    },
  ];

  return (
    <section id="about" className="about">
      <Header />

      <Container>
        <Title title={"A little bit about me"} />
        <Row className="about-row">
          <Col
            md={{ span: 6 }}
            className="
          animate__animated animate__fadeInLeftBig"
          >
            <p className="text-warning fw-bold fs-4 mb-0">About Me</p>
            <div className="about-underline mb-4 rounded-5" />
            <p className="about-me mb-1">
              I'm a Front End Engineer based in
              Egypt. I describe myself as a passionate developer who
              loves coding, open source, and the web platform.
            </p>
            <p className="about-me mb-0">
              Aside from my job, I like to create and contribute to open source
              projects. That helps me to learn a ton of new stuff, grow as a
              developer and support other open source projects.
            </p>
            <p className="text-warning fw-bold fs-4 mt-4 mb-0">Personal Info</p>
            <div className="about-underline mb-4 rounded-5" />

            {details.map((detail) => {
              return (
                <div className="info-card d-flex gap-3 align-items-center mb-2">
                  <span>{detail.open}</span>
                  <p className="mb-0">{detail.des}</p>
                </div>
              );
            })}
            <a
              href="https://drive.google.com/file/d/1JB2nKMplgAt1sHrouHGMcrGLaWKIX7g3/view?usp=share_link"
              target={"_blank"}
              rel={"noreferrer"}
              className="cv p-1 fw-bold mb-0 rounded-5"
            >
              Download CV
            </a>
          </Col>

          <Col
            md={{ span: 6 }}
            className="tech
          animate__animated animate__zoomInRight"
          >
            <p className="text-warning fw-bold fs-4 mb-0">Technologies</p>
            <div className="about-underline mb-4 rounded-5" />

            <Row className="d-flex justify-content-center text-center">
              {skills.map((skill) => {
                return (
                  <Col md={{ span: 4 }} xs={{ span: 4 }}>
                    <Card className="skill-card mb-3 p-1">
                      <p className="icon">{skill.icon}</p>
                      <p className="name fw-bold mb-0 fs-4">{skill.name}</p>
                      <p className="percent mb-0 mt-2 fw-bold text-warning">
                        {skill.per}
                      </p>
                    </Card>
                  </Col>
                );
              })}
            </Row>
          </Col>
        </Row>
        <Footer />
      </Container>
    </section>
  );
};

export default About;
