import React from "react";
import Header from "./Header";
import { Card, Col, Container, Row } from "react-bootstrap";
import Title from "./Title";
import {
  RiFacebookFill,
  RiInstagramFill,
  RiWhatsappFill,
  RiGithubFill,
  RiLinkedinFill,
} from "react-icons/ri";

import "../Styles/Contact.css";
import Footer from "./Footer";

const Contact = () => {
  const social = [
    {
      icon: <RiFacebookFill />,
      link: "https://www.facebook.com/e.mesery/",
    },
    {
      icon: <RiInstagramFill />,
      link: "https://www.instagram.com/mhmd_elmesery/",
    },
    {
      icon: <RiWhatsappFill />,
      link: "http://wa.link/0fabvv",
    },
    {
      icon: <RiGithubFill />,
      link: "https://github.com/melmesery/",
    },
    {
      icon: <RiLinkedinFill />,
      link: "https://www.linkedin.com/in/melmesery/",
    },
  ];

  const services = [
    {
      num: "1",
      des: "Optimizing the user experience.",
    },
    {
      num: "2",
      des: "Using HTML, JavaScript and CSS to bring concepts to life.",
    },
    {
      num: "3",
      des: "Responsive designs on mobile websites.",
    },
    {
      num: "4",
      des: "Creating tools that improve site interaction regardless of the browser.",
    },
    {
      num: "5",
      des: "Following SEO best practices.",
    },
    {
      num: "6",
      des: "Fixing bugs and testing for usability.",
    },
  ];
  return (
    <section id="contact" className="contact">
      <Header />
      <Container>
        <Title title={"Keep in touch"} />
        <Row
          className=" contact-row text-white d-flex justify-content-center animate__animated animate__zoomInDown"
        >
          <Col
            md={{ span: 7 }}
            xs={{ span: 10 }}
            className="mb-5
          animate__animated animate__fadeInLeftBig"
          >
            <p className="tITLE text-warning fw-bold fs-4 mb-0">Services</p>
            <div className="about-underline mb-4 rounded-5" />
            <Row>
              {services.map((service) => {
                return (
                  <Col md={{ span: 6 }}>
                    <div className="services-card gap-2 mb-2 p-1">
                      <div className="number w-25 fw-bold">{service.num}</div>
                      <div className="words w-75">{service.des}</div>
                    </div>
                  </Col>
                );
              })}
            </Row>
          </Col>
          <Col md={{ span: 3, offset: 1 }} xs={{ span: 10 }}>
            <p className="tITLE text-warning fw-bold fs-4 mb-0">Contact Me</p>
            <div className="about-underline mb-4 rounded-5" />
            <div className="d-flex gap-1 justify-content-center mb-3">
              {social.map((social) => {
                return (
                  <div className="social-container">
                    <a
                      href={social.link}
                      target={"_blank"}
                      rel={"noreferrer"}
                      className="social-icon text-white"
                    >
                      {social.icon}
                    </a>
                  </div>
                );
              })}
            </div>
            <Card className="contact-card p-3 mb-3 rounded-5">
              <h3 className="fw-bold">Email</h3>
              <div className="about-underline mb-4 rounded-5" />
              <p className="mb-0">melmesery@outlook.com</p>
              <p className="mb-0">mohamed_eldesouky@cic-cairo.com</p>
              <h3 className="fw-bold mt-4">Phone</h3>
              <div className="about-underline mb-4 rounded-5" />
              <p className="mb-0">(+20) 127-027-8442</p>
              <p className="mb-0">(040) 2130092</p>
            </Card>
          </Col>
        </Row>
        <Footer />
      </Container>
    </section>
  );
};

export default Contact;
