import React from "react";
import "../Styles/Header.css";
import { Link } from "react-router-dom";
import { TfiMenu, TfiClose } from "react-icons/tfi";
import { Container } from "react-bootstrap";

const Header = () => {
  const showMenu = () => {
    document.querySelector(".mobile-nav-container").classList.add("show");
  };
  const closeMenu = () => {
    document.querySelector(".mobile-nav-container").classList.remove("show");
  };
  const showBlur = () => {
    document.querySelector(".blur").classList.add("show");
  };
  const closeBlur = () => {
    document.querySelector(".blur").classList.remove("show");
  };
  return (
    <header className="header fixed-top">
      <Container>
        <div className="d-flex align-items-center justify-content-between py-2">
        <span
            className="mobile-btn fw-bold"
            onClick={() => {
              showMenu();
              showBlur();
            }}
          >
            <TfiMenu />
          </span>
          <Link to="/" className="title mb-0 fs-4">Mohamed El-Mesery </Link>
          <div className="main-nav gap-3">
            <Link to="/">Home</Link>
            <Link to="/about">About</Link>
            <Link to="/portfolio">Portfolio</Link>
            <Link to="/contact">Contact</Link>
          </div>
          <div
            className="mobile-nav-container  animate__animated animate__bounceInLeft"
            onClick={() => {
              closeMenu();
              closeBlur();
            }}
          >
            <Container>
              <span
                className="close-menu fw-bold"
                onClick={() => {
                  closeMenu();
                  closeBlur();
                }}
              >
                <TfiClose />
              </span>
              <div className="mobile-nav gap-4 text-center">
                <Link to="/">Home</Link>
                <Link to="/about">About</Link>
                <Link to="/portfolio">Portfolio</Link>
                <Link to="/contact">Contact</Link>
              </div>
            </Container>
          </div>
        </div>
        <div className="blur"></div>
      </Container>
    </header>
  );
};

export default Header;
